import { FaChevronLeft } from "react-icons/fa6";
import { useSidebar } from "../../contexts/sidebarContext";
import { useAddress } from "../../contexts/addressContext";
import { useCheckout } from "../../contexts/checkoutContext";
import AddressTab from "./AddressTab";

const AddressBar = () => {
  const { closeSidebar } = useSidebar();
  const { addresses } = useAddress();
  const {
    state: { billingAddress },
  } = useCheckout();

  return (
    <div className="bg-white h-svh relative">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p className="font-medium">Select Address</p>
      </div>

      <ul className="divide-y divide-secondary">
        <AddressTab />
        {billingAddress &&
          addresses.map((address, index) => (
            <AddressTab key={index} address={address} />
          ))}
      </ul>
    </div>
  );
};

export default AddressBar;
