import { FaTags } from "react-icons/fa";
import { FaRegCircleCheck } from "react-icons/fa6";
import InputBox from "../../components/InputBox";
import useForm from "../../hooks/useForm";
import { IoChevronForward } from "react-icons/io5";
import { useSidebar } from "../../contexts/sidebarContext";
import CouponList from "./CouponList";
import { useCheckout } from "../../contexts/checkoutContext";
import { useToast } from "../../contexts/toastContext";

const ApplyCoupon = () => {
  const { openSidebar } = useSidebar();
  const { state, applyCoupon, removeCoupon } = useCheckout();
  const toast = useToast();

  const onSubmitHandler = (data) => {
    const { couponCode } = data;

    // Client side validations
    if (!couponCode) {
      toast.error("Please enter a coupon code");
      return;
    } else {
      applyCoupon(couponCode);
    }
  };

  const { formData, handleChange, handleSubmit } = useForm({}, onSubmitHandler);

  return (
    <div className="p-3 md:border md:border-primary rounded-md divide-y-2 divide-secondary space-y-4">
      <form onSubmit={handleSubmit} className="h-8 flex gap-4">
        <InputBox
          placeholder="ENTER COUPON CODE"
          name="couponCode"
          value={formData.couponCode}
          onChange={handleChange}
          className="bg-white border border-primary rounded-md h-full w-full px-4 text-sm font-medium"
        />
        <button className="bg-warning text-sm font-semibold px-4 rounded-md">
          APPLY
        </button>
      </form>

      {state.coupon ? (
        <div className="flex items-center gap-4 p-3 w-full">
          <FaRegCircleCheck size={24} className="text-success" />
          <div>
            <p className="font-medium text-sm">{state.coupon} Applied</p>
            <p className="text-success text-xs"> {state.couponDiscount} OFF</p>
          </div>
          <button
            onClick={removeCoupon}
            className="ml-auto text-primeRed text-sm font-medium"
          >
            Remove
          </button>
        </div>
      ) : (
        <button
          onClick={() => openSidebar(<CouponList />)}
          className="flex items-center gap-4 p-3 w-full h-10"
        >
          <FaTags size={18} className="text-success" />
          <p className="text-success font-medium">View all coupons</p>
          <IoChevronForward size={18} className="text-success ml-auto" />
        </button>
      )}
    </div>
  );
};

export default ApplyCoupon;
