import { Fragment } from "react";
import { FaCheck } from "react-icons/fa";

const Stepper = ({ steps, currentStep, onClick }) => {
  return (
    <div className="w-full flex justify-around items-center gap-8 max-lg:gap-4 max-sm:gap-1">
      {steps?.map((step, index) => (
        <Fragment key={index}>
          <div className="STEP-ITEM max-md:w-16 flex max-md:flex-col items-center gap-4 max-lg:gap-3 max-md:gap-0 flex-shrink-0">
            <button
              type="button"
              onClick={() => onClick(index + 1)}
              className={`STEP-ICON size-8 max-lg:size-7 max-md:size-5 text-lg max-lg:text-base max-md:text-xs flex justify-center items-center rounded-full text-white transition-colors ease-out ${
                currentStep >= index + 1
                  ? "bg-primeRed delay-500"
                  : "bg-darkGray"
              }`}
            >
              {currentStep <= index + 1 ? step.icon : <FaCheck />}
            </button>
            <p
              className={`STEP-TEXT capitalize transition-colors ease-out max-md:text-xs ${
                currentStep >= index + 1
                  ? "text-black delay-500"
                  : "text-secondary"
              }`}
            >
              {step.name}
            </p>
          </div>

          {index !== steps.length - 1 && (
            <div className="relative h-[3px] w-full min-w-10 bg-darkGray overflow-hidden rounded-full">
              <span
                className={`absolute h-[3px] w-full bg-primeRed transition-transform duration-500 ease-out origin-left ${
                  currentStep < index + 2 ? "scale-x-0" : "scale-x-100"
                }`}
              ></span>
            </div>
          )}
        </Fragment>
      ))}
    </div>
  );
};

export default Stepper;
