import { motion } from "framer-motion";
import { useCheckout } from "../../contexts/checkoutContext";

const CouponCard = ({ coupon }) => {
  const { state, applyCoupon, removeCoupon } = useCheckout();
  const isCouponApplied = state.coupon === coupon.code;

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="bg-white border border-secondary rounded-md flex overflow-hidden"
    >
      <div className="OFFER-LABEL w-10 py-4 flex-shrink-0 bg-blue-500 text-center flex items-center justify-center">
        <div className="text-sm text-white font-semibold -rotate-90 whitespace-nowrap">
          {coupon.Discount_Type === "Percent"
            ? `${coupon.Discount_Value}% OFF`
            : ` ${coupon.Discount_Value} OFF`}
        </div>
      </div>

      <div className="CARD-BODY w-full p-2">
        <div className="flex justify-between items-center">
          <div className="text-lg font-semibold">{coupon.code}</div>
          {isCouponApplied ? (
            <button
              onClick={removeCoupon}
              className="text-red-500 font-semibold text-sm focus:outline-none"
            >
              REMOVE
            </button>
          ) : (
            <button
              onClick={() => applyCoupon(coupon.code)}
              className="text-blue-500 font-semibold text-sm focus:outline-none"
            >
              APPLY
            </button>
          )}
        </div>
        <p className="text-sm text-secondary mt-1 leading-none">
          Shop minimum of  {coupon.Minimum_Purchase} to avail this offer
        </p>
        <hr className="my-2 border-primary" />
        <p className="text-sm text-secondary">{coupon.title}</p>
        {/* <button className="text-sm text-blue-500 font-semibold mt-2 focus:outline-none">
          + MORE
        </button> */}
      </div>
    </motion.div>
  );
};

export default CouponCard;
