import { getCookie } from "../utils/cookie";
import axiosInstance from "./axiosInstance";

const createPayment = (amount, currency, orderId) => {
  const token = getCookie("accessToken");
  return axiosInstance.post(
    "/create-order",
    { amount, currency, orderId },
    {
      headers: {
        authorization: `Bearer ${token}`,
      },
    }
  );
};

const verifyPayment = (data) => {
  const token = getCookie("accessToken");
  return axiosInstance.post("/verify-payment", data, {
    headers: {
      authorization: `Bearer ${token}`,
    },
  });
};

const generatePayUHash = (amount, orderId) => {
  return axiosInstance.post("paymentDetail", { amount, orderId });
};

export { createPayment, verifyPayment, generatePayUHash };
