import { useCheckout } from "../../contexts/checkoutContext";

const PaymentBox = ({ label, method, icon, onClick }) => {
  const { state } = useCheckout();

  return (
    <button
      onClick={onClick}
      className="relative bg-white hover:bg-success hover:bg-opacity-30 border-2 border-secondary p-4 w-full max-md:w-full flex justify-between items-center gap-4 rounded-lg transition-colors duration-300 ease-in-out"
    >
      <div className="flex items-center gap-4">
        <span className="size-4 top-2 right-2 rounded-full bg-white border border-black flex justify-center items-center">
          <span
            className={`size-2 rounded-full ${
              state.paymentMethod === method && "bg-primeRed"
            }`}
          />
        </span>
        <p className="font-secondary font-medium">{label}</p>
      </div>
      <i className="text-2xl">{icon}</i>
    </button>
  );
};

export default PaymentBox;
