import logo from "../assets/images/Logo.png";
import loadScript from "../utils/loadScript";
import { useState } from "react";
import {
  createPayment,
  verifyPayment,
  generatePayUHash,
} from "../api/paymentApis";
import { useToast } from "../contexts/toastContext";
import { useUser } from "../contexts/userContext";

const usePayment = () => {
  const [payment, setPayment] = useState(null);
  const [paymentStatus, setPaymentStatus] = useState(null); // success, failure
  const [paymentLoading, setPaymentLoading] = useState(false);
  const { user } = useUser();
  const toast = useToast();

  const processPayment = async (paymentData, onSuccess) => {
    setPaymentLoading(true);
    setPaymentStatus(null);

    try {
      switch (paymentData.paymentMethod) {
        case "Razorpay":
          await initializeRazorpay(paymentData, onSuccess);
          break;
        case "PayU":
          await initializePayU(paymentData, onSuccess);
          break;
        case "Wallet":
          setPaymentStatus("success");
          onSuccess();
          break;
        case "COD":
          setPaymentStatus("success");
          onSuccess();
          break;
        default:
          throw new Error("Unsupported payment method");
      }
    } catch (error) {
      console.error("Error during payment processing:", error);
      setPaymentStatus("failed");
    } finally {
      setPaymentLoading(false);
    }
  };

  const initializeRazorpay = async (paymentData, onSuccess) => {
    const res = await loadScript(
      "https://checkout.razorpay.com/v1/checkout.js"
    );

    if (!res) {
      toast.error("Razorpay SDK failed to load. Are you online?");
      return;
    }

    const orderResponse = await createPayment(
      paymentData.amount,
      paymentData.currency,
      paymentData.orderId
    );

    if (!orderResponse) {
      toast.error("Server error. Are you online?");
      return;
    } else if (orderResponse?.status) {
      console.log(orderResponse.data);
      setPayment(orderResponse.data?.order);
    }

    const options = {
      key: process.env.REACT_APP_RAZORPAY_KEY,
      amount: orderResponse.data?.order?.amount.toString(),
      currency: orderResponse.data?.order?.currency,
      name: "Finafid Technologies Pvt.Ltd",
      description: "Test Transaction",
      order_id: orderResponse.data?.order?.id,
      image: logo,
      handler: async (response) => {
        const paymentResult = await verifyPayment(response);
        if (paymentResult?.status) {
          console.log(paymentResult.data);
          setPaymentStatus("success");
          onSuccess(paymentResult.data);
        }
      },
      prefill: {
        name: user.fullName,
        email: user.email,
        contact: user.phone,
      },
      notes: {
        address: paymentData.billingAddress,
      },
      theme: {
        color: "#c1272d",
      },
    };

    const paymentObject = new window.Razorpay(options);
    paymentObject.open();
  };

  const initializePayU = async (paymentData) => {
    const hashResponse = await generatePayUHash(
      paymentData.amount.toFixed(2),
      paymentData.orderId
    );

    if (!hashResponse || !hashResponse.data.paymentData.hash) {
      toast.error("Failed to initiate PayU payment. Please try again.");
      return;
    }

    // PayU configuration
    const payUConfig = {
      key: hashResponse.data.paymentData.key,
      txnid: paymentData.orderId.toString(),
      amount: paymentData.amount.toFixed(2),
      firstname: hashResponse.data.paymentData.firstname,
      email: hashResponse.data.paymentData.email,
      phone: hashResponse.data.paymentData.phone,
      productinfo: hashResponse.data.paymentData.productinfo,
      surl: hashResponse.data.paymentData.surl,
      furl: hashResponse.data.paymentData.furl,
      hash: hashResponse.data.paymentData.hash,
      service_provider: hashResponse.data.paymentData.service_provider,
    };

    const form = document.createElement("form");
    form.method = "POST";
    form.action = hashResponse.data.actionURL;

    Object.keys(payUConfig).forEach((key) => {
      const input = document.createElement("input");
      input.type = "hidden";
      input.name = key;
      input.value = payUConfig[key];
      form.appendChild(input);
    });

    document.body.appendChild(form);
    form.submit();
  };

  return {
    payment,
    processPayment,
    paymentStatus,
    paymentLoading,
  };
};

export default usePayment;
