import AddressForm from "../../components/AddressForm";
import Button from "../../components/Button";
import { useModal } from "../../contexts/modalContext";
import { FaCirclePlus } from "react-icons/fa6";
import { MdOutlinePhone } from "react-icons/md";
import { FiTrash2 } from "react-icons/fi";
import { useAddress } from "../../contexts/addressContext";
import { useCheckout } from "../../contexts/checkoutContext";

const AddressBox = ({ address }) => {
  const { openModal } = useModal();
  const { setDefault, removeAddress } = useAddress();
  const {
    state: { billingAddress },
    setBillingAddress,
  } = useCheckout();

  const handleDefaultAddress = (e) => {
    e.stopPropagation();
    setDefault(address._id);
  };

  const handleDeleteAddress = (e) => {
    e.stopPropagation();
    removeAddress(address._id);
  };

  return (
    <div className="w-64 h-44 border border-dashed border-darkGray rounded-md flex flex-shrink-0 justify-center items-center cursor-pointer bg-white overflow-hidden">
      {!address ? (
        <div
          onClick={() => openModal(<AddressForm />)}
          className="NO-ADDRESS size-full text-primeRed flex flex-col justify-center items-center gap-2 hover:bg-offWhite"
        >
          <FaCirclePlus size={24} />
          <p className="capitalize text-lg font-secondary font-medium">
            add new address
          </p>
        </div>
      ) : (
        <div
          onClick={() => setBillingAddress(address)}
          className="HAVE-ADDRESS relative size-full p-4 pt-6 flex flex-col items-start"
        >
          {address?.isDefault && (
            <span className="absolute top-[10px] right-10 text-[8px] text-white font-medium font-secondary px-1 rounded-sm pt-[1px] bg-success">
              DEFAULT
            </span>
          )}
          <span className="absolute top-2 right-2 size-4 rounded-full border border-black flex justify-center items-center">
            <span
              className={`size-2 rounded-full ${
                billingAddress._id === address._id && "bg-primeRed"
              }`}
            />
          </span>
          <div className="flex items-center justify-center gap-2 font-medium leading-tight">
            <h3>{address?.receiverName}</h3>
            <p className="uppercase text-xxs pt-[2px]">
              ({address?.addressType})
            </p>
          </div>
          <div className="flex justify-start items-center text-xs text-secondary">
            <MdOutlinePhone size={14} className="mr-2" />
            <p>{address?.receiverPhone || "N/A"}</p>
            {address?.alternatePhone && (
              <p>
                <span className="mx-1">/</span>
                {address.alternatePhone}
              </p>
            )}
          </div>

          <div className="uppercase text-xs font-medium text-secondary flex flex-wrap gap-x-1 mt-2">
            {address?.houseNumber && <p>{address.houseNumber},</p>}
            {address?.street && <p>{address.street},</p>}
            {address?.locality && <p>{address.locality},</p>}
            {address?.landmark && <p>{address.landmark},</p>}
            {address?.city && <p>{address.city},</p>}
            {address?.state && <p>{address.state},</p>}
            {address?.pinCode && <p>{address.pinCode}</p>}
          </div>

          <div className="BTN-SECTION w-full flex justify-between items-center gap-2 mt-auto">
            <Button
              reverse
              onClick={(e) => {
                e.stopPropagation();
                openModal(<AddressForm initialAddress={address} edit />);
              }}
              className="px-3 py-1 text-xs rounded-md font-secondary font-medium"
            >
              Edit
            </Button>
            {!address?.isDefault && (
              <Button
                onClick={handleDefaultAddress}
                className="text-xs px-6 py-1 rounded-md font-secondary"
              >
                set as default
              </Button>
            )}
            <button onClick={handleDeleteAddress}>
              <FiTrash2 size={18} className="text-primeRed" />
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default AddressBox;
