// import Button from "../../components/Button";
import QuantityInput from "../../components/QuantityInput";
import { useCheckout } from "../../contexts/checkoutContext";

const ItemBox = ({ product, quantity }) => {
  const { state, setBillingItems } = useCheckout();

  // const removeItem = (productId) => {
  //   const updatedItems = state.billingItems.filter(
  //     (item) => item.productId._id !== productId
  //   );
  //   setBillingItems(updatedItems);
  // };

  const increaseQuantity = (productId) => {
    const updatedItems = state.billingItems.map((item) =>
      item.productId._id === productId
        ? { ...item, itemQuantity: item.itemQuantity + 1 }
        : item
    );
    setBillingItems(updatedItems);
  };

  const decreaseQuantity = (productId) => {
    const updatedItems = state.billingItems
      .map((item) =>
        item.productId._id === productId
          ? { ...item, itemQuantity: item.itemQuantity - 1 }
          : item
      )
      .filter((item) => item.itemQuantity > 0);
    setBillingItems(updatedItems);
  };

  return (
    <div className="p-3 flex gap-3">
      <div className="LEFT-SECTION space-y-2">
        <div className="PRODUCT-IMAGE bg-white size-20 rounded flex-shrink-0">
          <img
            src={product?.images[0]}
            alt={product?.name}
            loading="lazy"
            className="size-full object-contain object-center rounded"
          />
        </div>
        <QuantityInput
          value={quantity}
          readOnly
          onIncrease={() => increaseQuantity(product._id)}
          onDecrease={() => decreaseQuantity(product._id)}
        />
      </div>

      <div className="RIGHT-SECTION">
        <h3 className="text-sm font-medium leading-none line-clamp-2">
          {product?.name}
        </h3>
        <ul className="ATTRIBUTES py-1">
          {Object.keys(product.attributes).map((key) => (
            <li
              key={key}
              className="text-sm max-lg:text-xs text-secondary capitalize"
            >
              {`${key} : ${product.attributes[key]}`}
            </li>
          ))}
        </ul>
        <div className="PRICING flex items-center gap-2">
          <p className="text-sm max-md:text-xs text-center font-medium text-primary line-through leading-none">
             {Math.round(product?.unitPrice) * quantity}
          </p>
          <p className="font-bold text-lg md:text-center leading-none">
             {Math.round(product?.sellingPrice) * quantity}
          </p>
          <span className="max-md:text-sm font-medium text-green-500 leading-none">
            {product?.discountType === "percent"
              ? `${product.discount}%`
              : `${product.discount} OFF`}
          </span>
        </div>
        <div className="UTSAV-PRICING flex items-center lg:gap-2 gap-1 whitespace-nowrap text-success">
          <p className="font-medium max-lg:text-sm">Utsav Price</p>
          <p className="text-md font-bold leading-none">
             {Math.round(product.utsavPrice) * quantity}
          </p>
        </div>
      </div>
    </div>
  );
};

export default ItemBox;
