import { MdOutlinePhone } from "react-icons/md";
import AddressForm from "../../components/AddressForm";
import { useModal } from "../../contexts/modalContext";
import { FaPlus } from "react-icons/fa6";
import { useAddress } from "../../contexts/addressContext";
import { useCheckout } from "../../contexts/checkoutContext";
import Button from "../../components/Button";
import { FiTrash2 } from "react-icons/fi";

const AddressTab = ({ address, preview = false }) => {
  const { openModal } = useModal();
  const { setDefault, removeAddress } = useAddress();
  const {
    state: { billingAddress },
    setBillingAddress,
  } = useCheckout();

  const handleDefaultAddress = (e) => {
    e.stopPropagation();
    setDefault(address._id);
  };

  const handleDeleteAddress = (e) => {
    e.stopPropagation();
    removeAddress(address._id);
  };

  return (
    <div className="w-full p-3">
      {!address ? (
        <div
          onClick={() => openModal(<AddressForm />)}
          className="NO-ADDRESS w-full py-4 flex items-center justify-center gap-2 shadow-md text-primeRed"
        >
          <FaPlus />
          <p className="capitalize font-medium">add new address</p>
        </div>
      ) : (
        <div
          onClick={() => setBillingAddress(address)}
          className="HAVE-ADDRESS w-full relative flex flex-col items-start"
        >
          <div className="absolute top-0 right-0 flex items-center gap-2">
            {address?.isDefault && (
              <span className="text-[8px] text-white font-medium font-secondary px-1 rounded-sm pt-[1px] bg-success">
                DEFAULT
              </span>
            )}
            {!preview && (
              <span className="size-4 rounded-full border border-black flex justify-center items-center">
                <span
                  className={`size-2 rounded-full ${
                    billingAddress._id === address._id && "bg-primeRed"
                  }`}
                />
              </span>
            )}
          </div>

          <div className="flex items-center justify-center gap-2 font-medium leading-none pr-12">
            <h3>{address?.receiverName}</h3>
            <p className="uppercase text-[8px] px-1 py-[3px] bg-mainGray rounded leading-none">
              {address?.addressType}
            </p>
          </div>
          <div className="flex justify-start items-center text-xs text-secondary">
            <MdOutlinePhone className="mr-2" />
            <p>{address?.receiverPhone || "N/A"}</p>
            {address?.alternatePhone && (
              <p>
                <span className="mx-1">/</span>
                {address.alternatePhone}
              </p>
            )}
          </div>

          <div className="text-sm font-medium text-secondary flex flex-wrap gap-x-1 mt-2">
            {address?.houseNumber && <p>{address.houseNumber},</p>}
            {address?.street && <p>{address.street},</p>}
            {address?.locality && <p>{address.locality},</p>}
            {address?.landmark && <p>{address.landmark},</p>}
            {address?.city && <p>{address.city},</p>}
            {address?.state && <p>{address.state},</p>}
            {address?.pinCode && <p>{address.pinCode}</p>}
          </div>

          {!preview && (
            <div className="BTN-SECTION w-full flex items-center justify-between gap-2 mt-4">
              <div className="flex items-center gap-2">
                <Button
                  reverse
                  onClick={(e) => {
                    e.stopPropagation();
                    openModal(<AddressForm initialAddress={address} edit />);
                  }}
                  className="px-3 py-1 text-xs rounded-md font-secondary font-medium"
                >
                  Edit
                </Button>
                {!address?.isDefault && (
                  <Button
                    onClick={handleDefaultAddress}
                    className="text-xs px-6 py-1 rounded-md font-secondary"
                  >
                    set as default
                  </Button>
                )}
              </div>
              <button onClick={handleDeleteAddress}>
                <FiTrash2 size={18} className="text-primeRed" />
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default AddressTab;
