import Button from "./Button";
import InputBox from "./InputBox";
import RadioInput from "./RadioInput";
import TextareaBox from "./TextareaBox";
import { useModal } from "../contexts/modalContext";
import useForm from "../hooks/useForm";
import { useToast } from "../contexts/toastContext";
import { useAddress } from "../contexts/addressContext";
import { isNameValid, isPhoneValid, isPinCodeValid } from "../utils/validation";
import { useState } from "react";
import {
  convertCoordinatesToAddress,
  getGeoLocation,
} from "../utils/locationUtils";
import { MdOutlineAddLocationAlt } from "react-icons/md";
import Loading from "./Loading";

const AddressForm = ({ initialAddress = {}, edit = false }) => {
  const toast = useToast();
  const { closeModal } = useModal();
  const { addAddress, updateAddress, loading } = useAddress();
  const [fetchingAddress, setFetchingAddress] = useState(false);

  const onSubmitHandler = async (addressData) => {
    const {
      _id,
      addressType,
      receiverName,
      receiverPhone,
      pinCode,
      locality,
      city,
      state,
    } = addressData;

    // Client side validations
    if (!addressType) {
      toast.error("Please select type of address");
      return;
    } else if (!isNameValid(receiverName).success) {
      toast.error(isNameValid(receiverName).message);
      return;
    } else if (!isPhoneValid(receiverPhone).success) {
      toast.error(isPhoneValid(receiverPhone).message);
      return;
    } else if (!isPinCodeValid(pinCode).success) {
      toast.error(isPinCodeValid(pinCode).message);
      return;
    } else if (!locality) {
      toast.error("Please enter locality");
      return;
    } else if (!city) {
      toast.error("Please enter city");
      return;
    } else if (!state) {
      toast.error("Please enter state");
      return;
    }

    if (edit) {
      updateAddress(_id, addressData);
      if (!loading) {
        clearForm();
        closeModal();
      }
    } else {
      addAddress(addressData);
      if (!loading) {
        clearForm();
        closeModal();
      }
    }
  };

  const { formData, setFormData, handleChange, handleSubmit, clearForm } =
    useForm(initialAddress, onSubmitHandler);

  const handleAutoFetchAddress = async () => {
    try {
      setFetchingAddress(true);
      const { latitude, longitude } = await getGeoLocation();

      const addressComponents = await convertCoordinatesToAddress(
        latitude,
        longitude
      );
      console.log(addressComponents);

      const getAddressComponent = (type) =>
        addressComponents.find((component) => component.types.includes(type))
          ?.long_name || "";

      setFormData({
        ...formData,
        houseNumber: getAddressComponent("street_number"),
        street: getAddressComponent("route"),
        locality:
          getAddressComponent("sublocality") || getAddressComponent("locality"),
        city:
          getAddressComponent("locality") ||
          getAddressComponent("administrative_area_level_2"),
        state: getAddressComponent("administrative_area_level_1"),
        pinCode: getAddressComponent("postal_code"),
        landmark: getAddressComponent("point_of_interest"),
      });
    } catch (error) {
      toast.error("Failed to fetch address. Please try again.");
    } finally {
      setFetchingAddress(false);
    }
  };

  return (
    <div className="relative overflow-hidden w-110 max-sm:w-svw max-sm:h-svh max-sm:overflow-y-auto bg-white sm:rounded-xl p-4 space-y-4">
      <h2 className="capitalize text-xl font-bold text-center">
        {!edit ? "add address" : "edit address"}
      </h2>

      <form onSubmit={handleSubmit} className="space-y-2">
        <div className="flex justify-between items-center">
          <div className="RADIO-INPUTS flex justify-center items-center gap-6">
            <RadioInput
              label="home"
              name="addressType"
              value="home"
              checked={formData.addressType === "home"}
              onChange={handleChange}
              containerClassName="gap-1"
            />
            <RadioInput
              label="office"
              name="addressType"
              value="office"
              checked={formData.addressType === "office"}
              onChange={handleChange}
              containerClassName="gap-1"
            />
            <RadioInput
              label="others"
              name="addressType"
              value="others"
              checked={formData.addressType === "others"}
              onChange={handleChange}
              containerClassName="gap-1"
            />
          </div>
          <button
            type="button"
            onClick={handleAutoFetchAddress}
            className="border-2 border-secondary rounded-md p-1 bg-white hover:bg-lightGray transition-colors duration-150 ease-in-out"
          >
            <MdOutlineAddLocationAlt className="text-primeRed text-lg" />
          </button>
        </div>

        <div className="grid sm:grid-cols-2 gap-2">
          <InputBox
            label="enter name"
            type="text"
            name="receiverName"
            value={formData.receiverName}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="mobile no"
            type="tel"
            name="receiverPhone"
            value={formData.receiverPhone}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="pin code"
            type="text"
            name="pinCode"
            value={formData.pinCode}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="house no"
            type="text"
            name="houseNumber"
            value={formData.houseNumber}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
        </div>
        <TextareaBox
          label="street"
          type="text"
          name="street"
          value={formData.street}
          onChange={handleChange}
          rows={3}
          containerClassName="gap-1 flex-col"
          labelClassName="capitalize font-medium text-xs"
        />
        <TextareaBox
          label="locality"
          type="text"
          name="locality"
          value={formData.locality}
          onChange={handleChange}
          rows={3}
          containerClassName="gap-1 flex-col"
          labelClassName="capitalize font-medium text-xs"
        />
        <div className="grid sm:grid-cols-2 gap-2">
          <InputBox
            label="city"
            type="text"
            name="city"
            value={formData.city}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="state"
            type="text"
            name="state"
            value={formData.state}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="landmark"
            type="text"
            name="landmark"
            value={formData.landmark}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
          <InputBox
            label="alternate number"
            type="tel"
            placeholder="alternate mobile (optional)"
            name="alternatePhone"
            value={formData.alternatePhone}
            onChange={handleChange}
            containerClassName="gap-1 flex-col"
            labelClassName="capitalize font-medium text-xs"
          />
        </div>

        <div className="flex items-center justify-end gap-4 pt-2">
          <Button
            type="button"
            reverse
            onClick={closeModal}
            className="h-8 px-4 rounded-md"
          >
            cancel
          </Button>
          <Button className="h-8 px-4 rounded-md">save</Button>
        </div>
      </form>

      {fetchingAddress && <Loading />}
    </div>
  );
};

export default AddressForm;
