import React, { useEffect, useState } from "react";
import { useCheckout } from "../../contexts/checkoutContext";
import { BsBagCheckFill, BsBagDashFill, BsBagXFill } from "react-icons/bs";
import { useCart } from "../../contexts/cartContext";
import { useNavigate } from "react-router-dom";

const Confirmation = () => {
  const { state, dispatch } = useCheckout();
  const { removeSelectedItemsFromCart } = useCart();
  const [countdown, setCountdown] = useState(3);
  const navigate = useNavigate();

  useEffect(() => {
    let timer;

    if (
      state.orderStatus === "success" ||
      state.orderStatus === "processing" ||
      state.orderStatus === "error"
    ) {
      timer = setInterval(() => {
        setCountdown((prevCountdown) => prevCountdown - 1);
      }, 1000);
    }

    const redirectTimer = setTimeout(() => {
      if (
        state.orderStatus === "success" ||
        state.orderStatus === "processing"
      ) {
        navigate(`/order/${state.orderId}`, { replace: true });
        removeSelectedItemsFromCart(state.billingItems);
        dispatch({ type: "RESET_ORDER" });
      } else if (state.orderStatus === "error") {
        navigate("/", { replace: true });
        dispatch({ type: "RESET_ORDER" });
      }
    }, 3000);

    return () => {
      clearTimeout(redirectTimer);
      clearInterval(timer);
    };
    // eslint-disable-next-line
  }, [state.orderStatus, state.orderId]);

  return (
    <div className="w-[500px] min-h-[400px] p-16 max-md:p-8 space-y-8 rounded-lg shadow-drop bg-lightGray">
      {state.orderStatus === "success" ? (
        <>
          <span>
            <BsBagCheckFill size={64} className="mx-auto text-success" />
          </span>
          <h3 className="font-secondary font-semibold text-xl text-center">
            Your Order is Confirmed - Thank You!
          </h3>
          <p className="text-sm text-secondary font-secondary text-center">
            Your order has been placed successfully, and we're thrilled to get
            your items on their way to you. We've sent a separate confirmation
            email with your order details (#[Order Number]).
          </p>
        </>
      ) : state.orderStatus === "processing" ? (
        <>
          <span>
            <BsBagDashFill size={64} className="mx-auto text-warning" />
          </span>
          <h3 className="font-secondary font-semibold text-xl text-center">
            Your Order is Being Processed!
          </h3>
          <p className="text-sm text-secondary font-secondary text-center">
            Thank you for your order (#[Order Number])! We've received it and
            are currently processing it to get it ready for shipment.
          </p>
        </>
      ) : (
        state.orderStatus === "error" && (
          <>
            <span>
              <BsBagXFill size={64} className="mx-auto text-error" />
            </span>
            <h3 className="font-secondary font-semibold text-xl text-center">
              Failed to place Order!
            </h3>
            <p className="text-sm text-secondary font-secondary text-center">
              Thank you for your recent attempt to order from (#[Order Number]).
              We appreciate your interest in our products. Unfortunately, we
              were unable to process your order due to a payment issue.
            </p>
          </>
        )
      )}

      <p className="text-xs text-primary text-center">
        Redirecting in {countdown}...
      </p>
    </div>
  );
};

export default Confirmation;
