import Logo from "../../components/Logo";
import Stepper from "../../components/Stepper";
import { useCheckout } from "../../contexts/checkoutContext";

const Nav = ({ steps }) => {
  const { state, toStep } = useCheckout();

  return (
    <header>
      <nav className="container max-w-full px-32 py-4 max-md:py-2 max-2xl:px-16 max-xl:px-8 max-sm:px-2 h-max flex max-md:flex-col justify-between items-center gap-16 max-md:gap-4 border-b border-primary">
        <a
          href="/"
          className="LOGO-SECTION h-12 max-xl:h-10 max-lg:h-8 max-md:h-6 max-sm:h-4 flex-shrink-0"
        >
          <Logo />
        </a>
        <Stepper steps={steps} currentStep={state.step} onClick={toStep} />
      </nav>
    </header>
  );
};

export default Nav;
