import usePayment from "./usePayment";
import { useCheckout } from "../contexts/checkoutContext";
import { createOrder, updateOrderStatus } from "../api/orderApis";

const useOrderPayment = () => {
  const { state, dispatch, toStep } = useCheckout();
  const {
    billingItems,
    billingAddress,
    paymentMethod,
    subtotal,
    discount,
    tax,
    shippingCost,
    total,
    walletBalance,
    couponDiscount,
  } = state;
  const { processPayment, paymentStatus, paymentLoading } = usePayment();

  const placeOrderAndPay = async () => {
    try {
      dispatch({ type: "SET_ORDER_STATUS", payload: "processing" });

      const orderResponse = await createOrder(
        billingItems,
        billingAddress,
        {
          tax,
          total,
          subtotal,
          discount,
          shippingCost,
          walletBalanceUsed: walletBalance,
          couponDiscount,
        },
        paymentMethod
      );

      if (orderResponse?.status) {
        const orderId = orderResponse.data?.newOrder?._id;
        dispatch({ type: "SET_ORDER_ID", payload: orderId });

        const onSuccess = async () => {
          try {
            console.log(orderResponse.data);
            await updateOrderStatus(orderId, "Confirmed");
            dispatch({ type: "SET_ORDER_STATUS", payload: "success" });
            toStep(4);
          } catch (error) {
            console.error("Error during Order confirmation:", error);
            toStep(4);
          }
        };

        await processPayment(
          {
            amount: total,
            currency: "INR",
            orderId,
            paymentMethod,
            billingAddress,
          },
          onSuccess
        );
      } else {
        throw new Error("Error placing order");
      }
    } catch (error) {
      console.error("Error during order processing:", error);
      dispatch({ type: "SET_ORDER_STATUS", payload: "error" });
      toStep(4);
    }
  };

  return {
    placeOrderAndPay,
    paymentStatus,
    paymentLoading,
  };
};

export default useOrderPayment;
