import Nav from "./Nav";
import BillingItems from "./BillingItems";
import BillingAddress from "./BillingAddress";
import PaymentDetails from "./PaymentDetails";
import OrderSummary from "./OrderSummary";
import { useCheckout } from "../../contexts/checkoutContext";
import { checkoutSteps } from "../../utils/Constants";
import Confirmation from "./Confirmation";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import ApplyWallet from "./ApplyWallet";
import ApplyCoupon from "./ApplyCoupon";

const Checkout = () => {
  const { state, dispatch } = useCheckout();
  const navigate = useNavigate();

  const renderStep = () => {
    switch (state.step) {
      case 1:
        return <BillingItems />;
      case 2:
        return (
          <>
            <BillingAddress />
            <BillingItems />
          </>
        );
      default:
        return <BillingItems />;
    }
  };

  useEffect(() => {
    if (state.billingItems.length === 0) {
      dispatch({ type: "SET_ORDER_STATUS", payload: "idle" });
      navigate(-1, { replace: true });
    }
  }, [state.billingItems, dispatch, navigate]);

  return (
    <main>
      <Nav steps={checkoutSteps} />

      {state.step < 3 ? (
        <section className="container max-w-full min-h-screen px-32 max-2xl:px-16 max-xl:px-8 max-sm:px-2 py-8 max-md:py-2 flex max-md:flex-col justify-center items-start gap-28 max-xl:gap-20 max-lg:gap-10">
          <section className="w-full space-y-10">{renderStep()}</section>
          <section className="w-80 max-md:w-full space-y-8 max-md:space-y-0 max-md:mb-28 flex-shrink-0">
            <ApplyWallet />
            <ApplyCoupon />
            <OrderSummary />
          </section>
        </section>
      ) : state.step === 3 ? (
        <section className="container max-w-[400px] py-8 max-md:p-2 flex flex-col justify-center items-center gap-8">
          <PaymentDetails />
          <OrderSummary />
        </section>
      ) : (
        state.step === 4 && (
          <section className="container max-w-full min-h-[80vh] py-8 max-md:p-2 flex justify-center items-center">
            <Confirmation />
          </section>
        )
      )}
    </main>
  );
};

export default Checkout;
