import Button from "../../components/Button";
import useOrderPayment from "../../hooks/useOrderPayment";
import { useCheckout } from "../../contexts/checkoutContext";
import { MdOutlineShoppingCart } from "react-icons/md";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useEffect } from "react";
import { useToast } from "../../contexts/toastContext";

const OrderSummary = () => {
  const maxMd = useMediaQuery("(min-width: 768px)");
  const toast = useToast();
  const { placeOrderAndPay, paymentLoading } = useOrderPayment();
  const { state, nextStep, toStep, setPaymentMethod } = useCheckout();

  useEffect(() => {
    if (state.total <= 0) {
      setPaymentMethod("Wallet");
    } else {
      setPaymentMethod(null);
    }
    // eslint-disable-next-line
  }, [state.total]);

  const handlePlaceOrder = async () => {
    if (!state.paymentMethod) {
      toast.warning(" select a payment method to proceed.");
      return;
    }
    await placeOrderAndPay();
  };

  const handleNextStep = () => {
    if (state.total === 0 && state.step === 2) {
      handlePlaceOrder();
    } else {
      nextStep();
    }
  };

  return (
    <div className="ORDER-SUMMARY w-full md:border md:border-primary md:rounded-md p-4 max-md:p-1">
      <h3 className="font-semibold font-secondary text-lg border-b-2 border-primary uppercase pb-2">
        Order Details
      </h3>

      <div className="flex items-center justify-between py-4 border-b border-primary">
        <div className="flex items-center gap-2">
          <span>
            <MdOutlineShoppingCart />
          </span>
          <span className="text-sm font-medium">
            {state.billingItems?.length} Item(s) in Cart
          </span>
        </div>

        <button
          type="button"
          onClick={() => toStep(1)}
          className="text-sm text-blue-500"
        >
          Details
        </button>
      </div>

      <div className="PRICING-DETAILS border-b border-primary py-4">
        <table className="w-full font-secondary">
          <tbody>
            <tr>
              <td className="text-sm text-secondary">
                Subtotal ({state.billingItems?.length})
              </td>
              <td className="text-right"> {state.subtotal}</td>
            </tr>
            <tr>
              <td className="text-sm text-secondary">Discount</td>
              <td className="text-right text-success"> -{state.discount}</td>
            </tr>
            <tr>
              <td className="text-sm text-secondary">GST (incl)</td>
              <td className="text-right text-primary">
                 {state.tax.toFixed(2)}
              </td>
            </tr>
            <tr>
              <td className="text-sm text-secondary">Shipping</td>
              <td className="text-right">
                {state.shippingCost > 0 ? (
                  ` +${state.shippingCost}`
                ) : (
                  <p className="text-xs font-medium text-green-500">
                    Free Delivery
                  </p>
                )}
              </td>
            </tr>
            {state.walletBalance > 0 && (
              <tr>
                <td className="text-sm text-secondary">Wallet Balance</td>
                <td className="text-right text-success">
                  -  {state.walletBalance}
                </td>
              </tr>
            )}
            {state.couponDiscount > 0 && (
              <tr>
                <td className="text-sm text-secondary">Coupon Discount</td>
                <td className="text-right text-success">
                  -  {state.couponDiscount}
                </td>
              </tr>
            )}
            <tr>
              <td className="font-semibold">Total</td>
              <td className="text-right text-lg font-medium">
                 {state.total}
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div
        className={`${
          !maxMd &&
          "bg-white fixed bottom-0 left-0 right-0 p-6 pt-0 shadow-upper"
        }`}
      >
        <div className="py-4 max-md:py-2">
          <div className="flex md:justify-center justify-between items-center">
            {!maxMd && (
              <div className="flex items-center gap-2">
                <span className="text-sm font-semibold font-secondary">
                  Total:
                </span>
                <span className="text-success font-medium">
                   {state.total}
                </span>
              </div>
            )}

            <div className="flex items-center gap-2">
              <span className="text-sm font-semibold font-secondary">
                Total Savings:
              </span>
              <span className="text-success font-medium">
                 {state.subtotal - state.total}
              </span>
            </div>
          </div>
        </div>

        {state.step < 3 && (
          <Button
            type="button"
            onClick={handleNextStep}
            className="w-full py-2 font-secondary font-medium"
          >
            {state.step === 2 ? "place order" : "continue"}
          </Button>
        )}

        {state.step === 3 && (
          <Button
            type="button"
            onClick={handlePlaceOrder}
            className="w-full py-2 font-secondary font-medium"
            disabled={paymentLoading}
          >
            {state.paymentMethod === "COD" ? "Confirm" : `Pay ${state.total}`}
          </Button>
        )}
      </div>
    </div>
  );
};

export default OrderSummary;
