import AddressBox from "./AddressBox";
import { useEffect } from "react";
import { useAddress } from "../../contexts/addressContext";
import { useCheckout } from "../../contexts/checkoutContext";
import { useSidebar } from "../../contexts/sidebarContext";
import useMediaQuery from "../../hooks/useMediaQuery";
import AddressTab from "./AddressTab";
import Button from "../../components/Button";
import AddressBar from "./AddressBar";

const BillingAddress = () => {
  const maxLg = useMediaQuery("(min-width: 1024px)");
  const maxMd = useMediaQuery("(min-width: 768px)");
  const { openSidebar } = useSidebar();
  const {
    state: { billingAddress },
    setBillingAddress,
  } = useCheckout();
  const { addresses, defaultAddress } = useAddress();

  useEffect(() => {
    setBillingAddress(defaultAddress);
    // eslint-disable-next-line
  }, [addresses]);

  return (
    <div className="space-y-4 max-md:space-y-0">
      <div className="flex justify-between items-center border-b-2 border-primary uppercase pb-2">
        <p className="font-semibold font-secondary text-lg max-sm:text-base">
          Delivery To
        </p>
        {!maxLg && (
          <Button
            reverse
            onClick={() => openSidebar(<AddressBar />)}
            className="px-2 py-1 rounded-md text-xs"
          >
            change
          </Button>
        )}
      </div>

      {maxMd ? (
        <ul className="flex justify-start gap-6 flex-wrap">
          <AddressBox />
          {billingAddress &&
            addresses.map((address, index) => (
              <AddressBox key={index} address={address} />
            ))}
        </ul>
      ) : (
        <AddressTab address={billingAddress} preview />
      )}
    </div>
  );
};

export default BillingAddress;
