import ItemBox from "./ItemBox";
import { useCheckout } from "../../contexts/checkoutContext";

const BillingItems = () => {
  const { state } = useCheckout();

  return (
    <div className="space-y-2">
      <div className="flex justify-between items-center border-b-2 border-primary uppercase pb-2">
        <p className="font-semibold font-secondary text-lg max-sm:text-base">
          Shopping Bag
        </p>
        <span className="text-dark text-sm font-medium">
          ({state.billingItems.length} Items)
        </span>
      </div>

      <ul className="divide-y divide-secondary">
        {state.billingItems.map((item) =>
          item.productId ? (
            <ItemBox
              key={item.productId._id}
              product={item.productId}
              quantity={item.itemQuantity}
            />
          ) : (
            <div key={item._id} className="text-error text-center text-sm p-1">
              Product not found
            </div>
          )
        )}
      </ul>
    </div>
  );
};

export default BillingItems;
