import CheckBox from "../../components/CheckBox";
import { useCheckout } from "../../contexts/checkoutContext";
import { useWallet } from "../../contexts/walletContext";
import useForm from "../../hooks/useForm";

const ApplyWallet = () => {
  const { balance } = useWallet();
  const {
    state: { walletBalance, total },
    applyWalletBalance,
  } = useCheckout();
  const { formData, handleChange } = useForm({ walletUsed: walletBalance > 0 });

  const handleWalletUsage = (e) => {
    const isChecked = e.target.checked;
    if (isChecked) {
      applyWalletBalance(balance);
    } else {
      applyWalletBalance(0);
    }
    handleChange(e);
  };

  return (
    <div className="md:border max-md:border-y border-primary p-3 md:rounded-md flex gap-x-4">
      <CheckBox
        name="walletUsed"
        checked={formData.walletUsed}
        onChange={handleWalletUsage}
      />
      <div className="leading-none space-y-1">
        <p className="text-primeRed font-medium">Pay using wallet</p>
        <p className="text-sm text-primary">
          Balance: {balance - walletBalance} / Total: {total}
        </p>
      </div>
    </div>
  );
};

export default ApplyWallet;
