import PaymentBox from "./PaymentBox";
import { useCheckout } from "../../contexts/checkoutContext";
import { paymentDetails } from "../../utils/Constants";

const PaymentDetails = () => {
  const { setPaymentMethod } = useCheckout();

  return (
    <div className="space-y-4 max-md:px-1">
      <div className="flex justify-between items-center border-b-2 border-primary uppercase pb-2">
        <p className="font-semibold font-secondary text-lg max-sm:text-base">
          Select Payment Method
        </p>
      </div>

      <div className="flex flex-wrap justify-start gap-x-16 gap-y-4">
        {paymentDetails.map((item) => (
          <PaymentBox
            key={item.label}
            label={item.label}
            method={item.method}
            icon={item.icon}
            onClick={() => setPaymentMethod(item.method)}
          />
        ))}
      </div>
    </div>
  );
};

export default PaymentDetails;
