import { useQuery } from "@tanstack/react-query";
import { getAllCoupons } from "../api/dealApis";

const useCoupons = () => {
  return useQuery({
    queryKey: ["coupons"],
    queryFn: getAllCoupons,
    staleTime: 1000 * 60 * 5,
    placeholderData: [],
  });
};

export { useCoupons };
