import { FaChevronLeft } from "react-icons/fa";
import { useSidebar } from "../../contexts/sidebarContext";
import { useCoupons } from "../../services/dealServices";
import CouponCard from "./CouponCard";

const CouponList = () => {
  const { closeSidebar } = useSidebar();
  const { data: coupons, isLoading, isFetching, error } = useCoupons();

  return (
    <div className="bg-white h-svh relative">
      <div className="HEADING px-4 py-2 border-b border-primary flex items-center gap-2">
        <FaChevronLeft onClick={closeSidebar} />
        <p className="font-medium">All Coupons</p>
      </div>

      {isLoading || isFetching ? (
        <div className="w-full">
          <h2 className="text-center font-secondary font-medium mt-8">
            Loading...
          </h2>
        </div>
      ) : error ? (
        <div className="w-full">
          <h2 className="text-center font-secondary font-medium mt-8">
            {error.message}
          </h2>
        </div>
      ) : (
        <ul className="divide-y divide-secondary space-y-4 p-3">
          {coupons.map((coupon) => (
            <CouponCard key={coupon._id} coupon={coupon} />
          ))}
        </ul>
      )}
    </div>
  );
};

export default CouponList;
